.lists {
	display: flex;
	max-width: 700px;
}

.list-1, .list-2 {
	width: 50%;
}

.list-2 {
	margin-left: 0.5rem;
}

.list-item {
	padding: 0.5rem;
	border: 1px solid #F0F0F0;
	margin-bottom: 0.5rem;
	cursor: pointer;
}

.list-item:hover {
	background-color: #F0F0F0;
}

.list-item--selected {
	background-color: aqua;
}
